
.bar{

    max-width: 1300px;
    margin: 0 auto;
    padding: 2rem 2rem;
    border-radius: 10px;
    background: linear-gradient(90deg, #10FCFD 0%, #011119 60%, #8513BE 100%);
    background-clip: text;
    color: transparent;
    box-shadow: 15px 15px 30px #01090e;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.primary-holder{

    display: flex;
    justify-content: space-around;
}

.primary-holder button{

    margin-left: 2rem;
}

.show{

    animation: fade-in 500ms forwards;

}

.hide{

    animation: fade-out 500ms forwards;
}

.task-form{

    max-width: 1300px;
    margin: 0 auto;
    padding: 2rem;
    background: linear-gradient(90deg, #10FCFD 0%, #011119 60%, #8513BE 100%);
    background-clip: text;
    color: transparent;
    box-shadow: 15px 15px 30px #01090e;
    transition: all 1s ease;
}

.task-form label{

    font-size: 1.5rem;
    display: block;
    margin: 2rem 0 1rem 0;
}

.task-form input,
.task-form textarea{

    width: 50%;
    border: none;
    outline: none;
    font-size: 1rem;
    padding: 10px 15px;
    border-radius: 8px;
    box-shadow:  inset 2px 2px 15px rgb(10, 10, 10),
                inset -2px -2px 15px rgb(65, 64, 64);
    background: #212121;
    color: white;
    font-weight: 600;
}

.task-form textarea{

    resize: none;
    margin-bottom: 1rem;
}

.search{

    margin-bottom: 2rem;
}

@keyframes fade-in{

    0% {
        opacity: 0;
        transform: translateY(-7%);
    }

    100% {

        opacity: 1;
        transform: translateY(0%);
    }
}

@keyframes fade-out{

    0% {
        opacity: 1;
        transform: translateY(0%);
    }

    100% {

        opacity: 0;
        transform: translateY(-7%);
        display: none;
    }
}


@media(max-width: 1000px){

    .task-form input,
    .task-form textarea{

        width: 100%;
    }
}