.App{

    min-height: 100vh;
    background: linear-gradient(32deg, rgba(64,180,255,1) 0%, rgba(1,17,25,1) 11%, rgba(1,17,25,1) 87%, rgba(133,19,190,1) 100%);
    position: relative;
    overflow: hidden;
}

.time-weather{

    max-width: 1300px;
    margin: 4rem auto;
    display: flex;
    gap: 10%;
}

h1{

    font-size: 3rem;
}

h2{

    font-size: 2rem;
}

h3{

    font-size: 1.5rem;
}

p{

    font-size: 1.1rem;
}

button {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    background: #212121;
    font-family: "Montserrat", sans-serif;
    box-shadow:  inset 2px 2px 15px rgb(10, 10, 10),
                inset -2px -2px 15px rgb(65, 64, 64);
    overflow: hidden;
    border: none;
    border-radius: 10px;
    cursor: pointer;
   }

   button:hover{

    box-shadow: 1px 1px 15px #8513BE,
                -1px -1px 15px #10FCFD;
   }
   
   button:after {
    content: " ";
    width: 0%;
    height: 100%;
    background: linear-gradient(32deg, rgba(64,180,255,1) 0%, rgba(1,17,25,1) 40%, rgba(1,17,25,1) 45%, rgba(133,19,190,1) 100%);
    position: absolute;
    transition: all 0.3s ease-in-out;
    left: 0;
   }
   
   button:hover::after {
    width: 110%;
   }
   
   button span {
    text-align: center;
    text-decoration: none;
    width: 100%;
    padding: 10px 15px;
    color: #fff;
    font-size: 1.125em;
    font-weight: 600;
    letter-spacing: 0.3em;
    z-index: 20;
    transition: all 0.3s ease-in-out;
   }
   

   hr{

    background: #8513BE;
    max-width: 1000px;
    height: 0.3rem;
    border: none;
    border-radius: 8px;
    margin: 4rem auto 4rem;
    box-shadow: 1px 1px 15px #8513BE,
                -1px -1px 15px #8513BE;
   }


   @media(max-width: 1000px){

    h1{

        font-size: 2.5rem;
    }
    
    h2{
    
        font-size: 1.5rem;
    }
    
    h3{
    
        font-size: 1rem;
    }
    
    p{
    
        font-size: 0.8rem;
    }

    hr{

        max-width: 80%;
    }

    .time-weather{

        max-width: 1300px;
        margin: 4rem auto;
        padding: 0 2rem;
        display: block;
    }
   }

   @media(max-width: 650px){

    h1{

        font-size: 2rem;
    }
    
    h2{
    
        font-size: 1rem;
    }
    
    h3{
    
        font-size: 1rem;
    }
    
    p{
    
        font-size: 1rem;
    }
   }