.shell{

    max-width: 1300px;
    margin: 0 auto;
    background: linear-gradient(90deg, #10FCFD 0%, #10FCFD 40%, #8513BE 100%);
    background-clip: text;
    color: transparent;
    padding: 2rem;
}

.pyramid-holder{

    max-width: 1300px;
    margin: 0 auto 2rem;
    padding: 3.5rem;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: linear-gradient(90deg, #10FCFD 0%, #8513BE 80%, #8513BE 100%);
    background-clip: text;
    color: transparent;
    box-shadow: 15px 15px 30px #01090e;
}

.pyramid-holder h2,
.pyramid-holder h3{

    margin-bottom: 1rem;
}

.pyramid-loader {
    position: relative;
    width: 300px;
    height: 150px;
    display: block;
    transform-style: preserve-3d;
    transform: rotateX(-20deg);
  }
  
  .wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    animation: spin 4s linear infinite;
  }
  
  @keyframes spin {
    100% {
      transform: rotateY(360deg);
    }
  }
  
  .pyramid-loader .wrapper .side {
    width: 150px;
    height: 150px;
    background: linear-gradient(to bottom right, #10FCFD, #8513BE);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    transform-origin: center top;
    clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
  }
  
  .pyramid-loader .wrapper .side1 {
    transform: rotateZ(-30deg) rotateY(90deg);
    background: linear-gradient(to bottom right, #8513BE, #10FCFD);
  }
  
  .pyramid-loader .wrapper .side2 {
    transform: rotateZ(30deg) rotateY(90deg);
    background: linear-gradient(to bottom right, #10FCFD, #8513BE);
  }
  
  .pyramid-loader .wrapper .side3 {
    transform: rotateX(30deg);
    background: linear-gradient(to bottom right, #10FCFD, #8513BE);
  }
  
  .pyramid-loader .wrapper .side4 {
    transform: rotateX(-30deg);
    background: linear-gradient(to bottom right, #8513BE, #10FCFD);
  }
  
  .pyramid-loader .wrapper .shadow {
    width: 150px;
    height: 150px;
    background: #8513BE;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    transform: rotateX(90deg) translateZ(-80px);
    filter: blur(25px);
  }


  .shell{

    margin-bottom: 2rem;
    box-shadow: 15px 15px 30px #01090e;
    padding: 1rem;
  }

  .completed{

    height: 80px;
    width: 80px;
    border-radius: 50%;
    box-shadow: inset 0px 0px 15px rgb(14, 14, 14),
                inset -3px -3px 5px rgb(65, 64, 64);
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgb(1, 10, 15);
  }


  .individual-task,
  .individual-task-conrols{

    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .main-information{

    width: 50%;
  }

  .individual-task-conrols{

    width: 20%;
  }

  .completed svg{

    color: #13be2f;
    border-radius: 50%;
    box-shadow: 2px 2px 15px rgb(1, 104, 35),
                -2px -2px 15px rgb(65, 64, 64);
  }

  .main-information h3{

    margin-bottom: 1rem;
    letter-spacing: 3px;
  }

  .main-information p{

    line-height: 1.5rem;
    font-weight: 600;
    text-align: justify;
  }

  .neon-devider{

    background: #10FCFD;
    width: 500px;
  }

  .outer-description{

    display: none;
  }

  @media(max-width: 1100px){

    
  .main-information{

    width: 50%;
  }

  .individual-task-conrols{

    width: 30%;
  }

  .individual-task-conrols{

    flex-direction: column;
    align-items: end;
  }

  .individual-task-conrols button{

    margin-bottom: 1rem;
  }

  }

  @media(max-width: 750px){

    .inner-description{

      display: none;
    }

    .outer-description{

      display: block;
      text-align: justify;
      padding: 0 1rem;
      line-height: 1.5rem;
    }

    .completed{

      margin-right: 1rem;
    }
  }