.weather-display,
.city-selector{

    box-shadow: 15px 15px 30px #010a0f;
    width: 45%;
    border-radius: 10px;
    padding: 2rem;
    background: linear-gradient(45deg, #011119, #10FCFD, #8513BE);
    background-clip: text;
    color: transparent;

}

.city-selector{

    display: block;
    padding: 10px;
    border: none;
    width: 100%;
    text-align: center;
    font-size: 1.5rem;
    font-weight: 600;
}

.inner-info,
.info{

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.info{

    padding-top: 1rem;
}

.inner-info h2 svg{

    color: #8513BE;
}


.info img{

    width: 100px;
    float: left;
}

.info h2{

    text-align: center;
}

.search{

    width: 100%;
    display: flex;
}

.search input{

    width: 70%;
    border: none;
    outline: none;
    padding: 10px 15px;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    box-shadow:  inset 2px 2px 15px rgb(10, 10, 10),
                inset -2px -2px 15px rgb(65, 64, 64);
    background: #212121;
    color: #10FCFD;
    font-size: 1rem;
}

.search button{

    width: 30%;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
}

@media(max-width: 1000px){

    .weather-display{

        width: 100%;
    }
}