@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@300;400;600&family=Poppins:wght@400;600;700&display=swap');


*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Barlow', sans-serif;
}

/* width */
::-webkit-scrollbar {
  width: 15px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgb(10, 10, 10);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(2, 31, 46);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(3, 43, 63);
}