.date-dispaly{

    box-shadow: 15px 15px 30px #010a0f;
    width: 45%;
    border-radius: 10px;
    padding: 2rem;
    background: linear-gradient(45deg, #011119, #10FCFD, #8513BE);
    background-clip: text;
    color: transparent;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.date-dispaly h1{

    color: linear-gradient(black, green);
}

.date-dispaly p svg{

    color: #8513BE;
    border-radius: 50%;
    box-shadow: 1px 1px 15px #8513BE,
                -1px -1px 15px #8513BE;
}

@media(max-width: 1000px){

    .date-dispaly{

        width: 100%;
    }
}