.header{

    width: 100%;
    padding: 2rem 0;
    color: rgb(230, 230, 230);
    box-shadow: 15px 15px 30px #01090e8c;
}

.header-content{

    max-width: 1300px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    padding: 0 2rem;
}

.header img{

    width: 50px;
    box-shadow: 5px 5px 15px #8513BE,
                -5px -5px 15px #10FCFD;
    margin-right: 3rem;
}